import { Injectable } from '@angular/core';

import { Observable, of } from 'rxjs'

import { WorkDetail } from '../shared/work-detail';
import { WorkDetails } from '../shared/work-details';
import { Skill } from '../skills/skill';
import { Skills } from '../skills/skills';

@Injectable({
  providedIn: 'root'
})
export class SharedService {

  constructor() { }

  getWorkDetails(): Observable<WorkDetail[]> {
    return of(WorkDetails);
  }
  getWorkDetail(employer_id: string): Observable<WorkDetail> {
    return of(WorkDetails.filter((workdetail)=> (workdetail.employer_id === employer_id))[0]);
  }
  
  getSkills(): Observable<Skill[]> {
    return of(Skills);
  }
}
