import { Component, OnInit } from '@angular/core';
import { SharedService } from '../services/shared.service';

import * as $ from 'jquery';

import { WorkDetail } from '../shared/work-detail';
import { Testimonial } from '../shared/testimonials';
import { flyInOut } from '../animations/app.animations';

@Component({
  selector: 'app-testimonials',
  templateUrl: './testimonials.component.html',
  styleUrls: ['./testimonials.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
    },
  animations: [
    flyInOut()
  ]
})
export class TestimonialsComponent implements OnInit {

  workDetails: WorkDetail[];
  show = false;

  constructor(private sharedService: SharedService) { }

  ngOnInit(): void {
      this.sharedService.getWorkDetails().subscribe(workdetails => {this.workDetails = workdetails;});
  }

  getTestimonial(employer_id, first_name): Testimonial {
    return this.workDetails.filter(function(workDetail) {
      return workDetail.employer_id == employer_id;
    })[0].testimonials.filter(function(testimonial) {
        return testimonial.first_name == first_name;
    })[0];
  }

  showHide(id): void {
    this.show = !this.show;
    if(this.show) {
      $("#"+id).removeClass("hide");
    } else {
      $("#"+id).addClass("hide");
    }
  }

}
