import { ViewEncapsulation, Component, OnInit } from '@angular/core';
import { CloudOptions } from 'angular-tag-cloud-module';

import { SharedService } from '../services/shared.service';
import { Skill } from './skill';
import { flyInOut } from '../animations/app.animations';

@Component({
  selector: 'app-skills',
  templateUrl: './skills.component.html',
  styleUrls: ['./skills.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
    },
  animations: [
    flyInOut()
  ]
})
export class SkillsComponent implements OnInit {
  
  skillsData: Skill[];
  skills: Skill[];
  categories: String[] =['', 'programming_languauge', 'tool', 'certifications', 'cloud', 'database', 'industry', 'domain'];
  options: CloudOptions = {
    // if width is between 0 and 1 it will be set to the width of the upper element multiplied by the value
    width: 1150,
    // if height is between 0 and 1 it will be set to the height of the upper element multiplied by the value
    height: 500,
    overflow: false,
  };
  show = false;
  type: String = 'All';
  constructor(private sharedService: SharedService) { }

  ngOnInit(): void {
    this.sharedService.getSkills().subscribe(skills => {this.skillsData = skills;});

    this.skills = this.skillsData;
  }

  showNav(): void {
    this.show = !this.show;
  }

  filterSkillsSM(category, type): void {
    this.show = !this.show;
    this.type = type;
    this.skills = this.skillsData.filter(function(skill) {
      if(!category){
        return true;
      } else {
        return skill.category == category;
      }
    });
  }

  filterSkills(tab): void {
    var categories = this.categories;
    this.skills = this.skillsData.filter(function(skill) {
      if(!categories[tab.index]){
        return true;
      } else {
        return skill.category == categories[tab.index];
      }
    });
  }
}
