import { Component, OnInit } from '@angular/core';

import {Params, ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { switchMap } from 'rxjs/operators';
import { Router } from '@angular/router';

import { WorkDetail } from '../shared/work-detail'
import { SharedService } from '../services/shared.service';
import { flyInOut } from '../animations/app.animations';


@Component({
  selector: 'app-work-detail',
  templateUrl: './work-detail.component.html',
  styleUrls: ['./work-detail.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
    },
  animations: [
    flyInOut()
    // visibility()
  ]
})
export class WorkDetailComponent implements OnInit {

  workDetail: WorkDetail;
  visibility_state = 'shown';

  constructor(private sharedService: SharedService,
    private route: ActivatedRoute,
    private location: Location,
    private router: Router) { }

  ngOnInit(): void {
    // this.visibility_state = 'shown';
    this.route.params.pipe(switchMap((params: Params)=> 
      this.sharedService.getWorkDetail(params['employer_id'])))
      .subscribe(workdetail => {this.workDetail = workdetail;});
    }
    goBack(): void {
      this.visibility_state = 'hidden';
      var routerTemp = this.router, locationTemp = this.location;
      setTimeout(goToWork, 500);
      function goToWork() {
        locationTemp.back();
      }
    }

}
